@font-face{
    font-family: nunito;
    src: url(/atividadereact/static/media/Nunito-LightItalic.abb8821a.ttf);
    font-weight: normal;
}

body{
    background-color: #5c3c8c;

}

.App{
    text-align: center;
    color: white;
    font-family: nunito;
}
.App h2{
    font-size: 20px
} 
.logo{
    text-align: center;
}

.logo img{
    max-width: 10%;
}

.armazenar{
    margin: 24px 0;
}

.armazenar input{
    flex-direction: column;
    align-items: center;
    margin-bottom: 20px;
    border-radius: 20px;
    background-color: transparent;
    border-color: white;
    color: white;
    border: 4px solid;
    width: 20%;
    padding: 10px;
}

button{
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #000000;
    width: 23%;
    border-radius: 10px;
    font-size: 18px;
    padding: 10px;
    border: none;
    cursor: pointer;
    color: #ffffff;
    }
    
    
